<template>

  <div>
    <h3>资源商分组编辑</h3>
    <Divider orientation="left">信息填写</Divider>
    <Row>
      <Col>
        <Form :model="formData" :label-width="100" style="width: 50%">
          <FormItem label="分组名称">
            <Input v-model="formData.name" placeholder="请填写分组名称"></Input>
          </FormItem>
          <FormItem label="分组名称">
            <InputNumber v-model="formData.sort" placeholder="请填写分组排序" style="width: 100%"></InputNumber>
          </FormItem>
          <FormItem>
            <Button type="success" long @click="submit">提交</Button>
          </FormItem>
        </Form>
        <Spin size="large" fix v-if="load"></Spin>
      </Col>

    </Row>
  </div>
</template>

<script>
import api from './api'

export default {
  name: 'edit',
  data () {
    return {
      formData: {
        status: 1
      },
      picture: null,
      load: false
    }
  },
  mounted () {
    if (this.$route.query.id != null) {
      this.loadData()
    }
  },
  methods: {
    loadData () {
      this.load = true
      api.detail({ id: this.$route.query.id }, res => {
        this.load = false
        this.formData = res.data.data
        this.formData.space_id = this.formData.space.id
      }, () => {
        this.load = false
      })
    },
    submit () {
      api.save(this.formData, () => {
        this.load = false
        this.$router.push({ name: 'group' })
      }, () => {
        this.load = false
      })
    }
  }
}
</script>

